  manageGallery2(loading) : void
  {
    let creat='Actualiza';


    if(this.username=='')
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');
      loading.dismiss();
      return;
    }
    if(!this.username)
    {
      this.msj.showError3('Mensaje','Existe un problema con su dispositivo , no existe usuario');
      loading.dismiss();
      return;
    }




     let   options       : any	     = {
      operacion:'actualizarinfor',
     foto1 : this.imagen,
     foto2 : this.imagen2,
     foto4 : this.imagen4,
     foto3 : this.imagen3,

     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' asigna calusac '}
    };


    loading.dismiss();
       if(options){
          this.authService.createReg(this.navParams.data.record._id,options,'asignacalusacs').then((result) => {
             if (!this.form.valid) { return; }

             this.viewCtrl.dismiss(result);
         }, (err) => {
          this.msj.showError3('Error',err.error);
         });
      }






  }

