  validacarne() {
//validar carnet  y codigo nivel
    this.comprobantenivel=false
    this.carnecalusac=this.carnecalusac1
    this.authService.getReg( this.carnecalusac+'/'+ this.codigonn + '/' +  this.codigounidad ,'asignanotas').then((result) => {
      this.vvalidanivel=result;


      //recorrer this.vvalidanivel[i].nota  la sumatoria > 70
   var nota=0
      for (var i = 0; i < this.vvalidanivel.length; i++)
			{
        nota=nota +this.vvalidanivel[i].nota
      }

      if(nota>=70)
      {
        this.sinivel='1';
        this.comprobantenivel=false;
        this.msj.showError3('Exito','Nivel '+  this.codigonn +' Aprobado , continue con la asignación ');

      }
      else{
        this.msj.showError3('Error','Nivel no aprobado en base de datos , Ingrese una foto de constacia donde aprobó el nivel '  + this.codigonn +' para autorización');

        this.sinivel='0';
        this.comprobantenivel=true;
      }

    }, (err) => {
    this.msj.showError3('Error','Nivel no aprobado en base de datos , Ingrese una foto de constacia donde aprobó el nivel '  + this.codigonn +' para autorización');
    this.sinivel='0';
    this.comprobantenivel=true;
    });

  }

