  updateProfileImage(op) {
    this.opimg=op;
    let t1,t2;
    this.translateService.get('SELECCIONE_IMAGEN').subscribe((value) => {
      t1=value;
    });
    this.translateService.get('TOME_IMAGEN').subscribe((value) => {
      t2=value;
    });
    let actionSheet = this.actionSheetCtrl.create({
      title: t1,
      buttons: [//1930
        {
          text: t2,
          handler: () => {
            this.takePhotograph(op);
          }
        },
        {
          text: t1,
          handler: () => {
            this.selectImage(op);
          }
        }
      ]
    });
    actionSheet.present();
  }

  takePhotograph(op) : void
  {
    this.opimg=op;
   if (Camera['installed']()) {
     this._IMAGE
     .takePhotograph()
     .then((image)=>
     {

      switch (op) {
        case '1':
        this.imagen   	= image.toString();
        this.image   		= image.toString();
        break
        case '2':
        this.imagen2   	= image.toString();
        this.image2   		= image.toString();
        break
        case '3':
        this.imagen3   	= image.toString();
        this.image3   		= image.toString();
        break
        case '4':
        this.imagen4   	= image.toString();
        this.image4   		= image.toString();
        break
        default:
    }


     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{

    switch (op) {
      case '1':
          this.fileInput.nativeElement.click();
      break
      case '2':
          this.fileInput2.nativeElement.click();
      break
      case '3':
          this.fileInput3.nativeElement.click();
      break
      case '4':
          this.fileInput4.nativeElement.click();
      break
      default:

    }

   }
  }


  selectImage(op) : void
  { this.opimg=op;
   if (Camera['installed']()) {
     this._IMAGE
     .selectPhotograph()
     .then((image)=>
     {
      switch (op) {
        case '1':
        this.imagen   	= image.toString();
        this.image   		= image.toString();
        break
        case '2':
        this.imagen2   	= image.toString();
        this.image2   		= image.toString();
        break
        case '3':
        this.imagen3   	= image.toString();
        this.image3   		= image.toString();
        break
        case '4':
        this.imagen4   	= image.toString();
        this.image4   		= image.toString();
        break
        default:
      }
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{


    switch (op) {
      case '1':
        this.fileInput.nativeElement.click();
      break
      case '2':
        this.fileInput2.nativeElement.click();
      break
      case '3':
        this.fileInput3.nativeElement.click();
      break
      case '4':
        this.fileInput4.nativeElement.click();
      break
      default:


   }       }
  }

  public processWebImage(event): void {
    if(event.target.files && event.target.files[0]){
          const reader: FileReader = new FileReader();
          reader.onload = (readerEvent: any) => {

            switch (this.opimg) {
              case '1':
                  this.image = readerEvent.target.result;
                  this._IMAGE.generateFromImage(this.image, 800, 800, 1, data => {
                    this.image = data;
                    this.imagen =  data;
                });
              break
              case '2':
                  this.image2 = readerEvent.target.result;
                  this._IMAGE.generateFromImage(this.image2, 800, 800, 1, data => {
                    this.image2 = data;
                    this.imagen2 =  data;
                });
              break
              case '3':
                  this.image3 = readerEvent.target.result;
                  this._IMAGE.generateFromImage(this.image3, 800, 800, 1, data => {
                    this.image3 = data;
                    this.imagen3 =  data;
                });
              break
              case '4':
                  this.image4 = readerEvent.target.result;
                  this._IMAGE.generateFromImage(this.image4, 800, 800, 1, data => {
                    this.image4 = data;
                    this.imagen4 =  data;
                });
              break

              default:
              }




   };
      reader.readAsDataURL(event.target.files[0]);
    }
}



